.App .container .languageSwitcher {
  color: #ff7070;
  font-size: 30px;
}

.App .container .location {
  color: #ffa750;
  font-size: 22px;
  font-weight: 600;
  margin-top: 20px;
}

.App .container .temperature {
  color: #509cff;
  font-size: 30px;
  font-weight: 600;
  margin-top: 30px;
}

.App .container .description {
  color: #646464;
  font-size: 42px;
  font-weight: 700;
  line-height: 46px;
  margin-top: 10px;
  white-space: pre-wrap;
}

.App .container .lookOutside {
  color: #ff7070;
  font-size: 20px;
  font-weight: 600;
  margin-top: 10px;
}

.App .container .footer {
  color: #3b3b3b;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  margin-top: 55px;
}

.loading {
  color: #ff7070;
  font-size: 20px;
  position: absolute;
  top: 50%;
  text-align: center;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
}

.Landscape {
  color: #ff7070;
  font-size: 22px;
  text-align: center;
  margin-top: 80px;
}

.red {
  color: #ff7070;
}

a,
a:hover,
a:focus,
a:visited,
a:active {
  text-decoration: underline;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
  color: #3b3b3b;
}

@media only screen and (min-width: 600px) {
  .App {
    text-align: center;
    overflow: hidden;
  }
  .App .container {
    width: 60%;
    -webkit-box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.1);
            box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

@media only screen and (min-width: 1200px) {
  .App .container {
    width: 30%;
  }
}



